html {
  box-sizing: border-box;
  font-size: 16px;
  /*  */
  /* overflow-y: hidden; */
  /*  */
}

*, *:before, *:after {
  box-sizing: inherit;
  letter-spacing: 0.2px;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
  background-color: rgb(172, 178, 184);
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}